.order {
    width: 100%;
    margin-top: 50px;

    @include respond-min(md) {
        margin-top: 20px;
        padding-bottom: 30px;
        overflow: hidden;
    }

    &__inner {
        position: relative;
        display: flex;
        justify-content: center;
        max-width: 1200px;
        width: 100%;
        margin: auto;

        @include respond-min(md) {
            justify-content: space-between;
        }
    } //.order__inner

    &__title {
        padding-right: 21px;
        font-family: $f-medium;
        color: #fff;
        @include flexCenter;
        justify-content: center;
        flex: 1;

        @include respond-min(md) {
            font-family: $f;
            font-size: 20px;
            color: $gray--darkest;
        }

        &--mobile {

            @include respond-min(md) {
                display: none;
            }
        } //.order__title--mobile
    } //.order__title

    &__top-line {
        position: fixed;
        top: 0;
        width: 100%;
        padding: 11.5px 20px;
        background-color: $color-bg--dark;
        z-index: 110;

        @include respond-min(md) {
            position: relative;
            padding: 0 0 20px;
            margin-bottom: 30px;
            background-color: #fff;
            border-bottom: 1px solid $color-bg--lightest;
        }

        .button-link--icon {
            margin-right: auto;
            margin-top: 4px;

            i {
                font-size: 20px;
            }
        }
    } //.order__top-line

    &__left {
        width: 100%;

        @include respond-min(md) {
            @include flexColumn;
            width: 65%;
            margin-right: 30px;
        }

        &_top {
            position: absolute;
            width: 100%;
            top: -100%;
            left: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 10px;
            background-color: #fff;

            @include respond-min(md) {
                display: flex;
                position: relative;
                width: 100%;
                padding: 0;
                margin: 0;
                margin-bottom: 30px;
                border-bottom: none;
            }

            &.is-active {
                top: 0;
                z-index: 20;
                border-bottom: 1px solid $color-border--lightest;
                @include tr;

                & + .service-list {
                    position: fixed;
                    top: 143px;
                    right: 0;
                    width: 100%;
                    bottom: 0;
                    background-color: #fff;
                    overflow-y: auto;
                    @include tr;

                    @include respond-min(md) {
                        position: relative;
                        top: 0;
                        overflow-y: visible;
                    }
                }

                @include respond-min(md) {
                    top: 0;
                }
            }
        }
    } //.order__left

    &__right {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        background-color: #fff;

        @include respond-min(md) {
            position: relative;
            top: 0;
            width: 32%;
        }
    } //.order__right

    .icon {

        &:hover {
            cursor: pointer;
        } //.icon:hover

        &--delete {
            position: absolute;
            left: 70px;
            top: 50%;
            transform: translateY(-50%);
            width: 20px;
            height: 20px;
            overflow: hidden;
            @include tr;

            .fa-minus-circle {
                font-size: 20px;
                color: $color-danger;
            }
        } //.icon--delete
    } //.icon

    .modal {

        &-dialog {
            display: flex;
            justify-content: center;
        } //.modal-dialog

        &-header {
            justify-content: center;
        }

        &-content {

            @include respond-min(md) {
                max-width: 330px;
            }
        } //.modal-content

        &-title {
            font-size: 17px;
            font-family: $f-medium;
        } //.modal-title

        &-body {

            &__scroll-wrap {
                height: calc(100vh - 134px);
                overflow-y: auto;
                @include bb-scroll--thin;
            }

            &.no-padding {
                padding: 0;
            }
        }

        &-footer {
            display: flex;
            justify-content: center;
            margin-top: auto;

            @include respond-min(md) {
                padding: 17px;
            }
        } //.modal-footer

        &--inside-scroll {

            .modal-content {
                width: 100%;
                height: calc(100vh - 30px);
            }
        }
    } //.modal

    .bb-checkbox {

        &__box {
            position: relative;
            margin-bottom: 5px;
        }

        &--master {
            display: flex;
            align-items: center;
            padding-left: 0;
        }
    } //.bb-checkbox
} //.order

.master-picker {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 15px 17px;
    border-bottom: 1px solid $color-border--lightest;

    @include respond-min(md) {
        margin-bottom: 30px;
        border: 1px solid $color-border--lightest;
    }

    &:hover {
        cursor: default;
    }

    &__item {
        position: relative;
        height: 70px;
        padding: 10px 15px;

        & + .master-picker__item {
            border-top: 1px solid $color-border--lightest;
        } //.modal-box__item + .modal-box__item

        .user {
            display: flex;
            align-items: center;
            margin-right: auto;

            &__img {
                width: 50px;
                height: 50px;
            } //.user__img

            &__avatar {
                margin-bottom: 0;
                margin-right: 13px;
            } //.user__avatar
        } //.user
    } //.master-picker__item

    &__title {
        display: flex;
        flex-direction: column;
        justify-content: center;

        &_top {
            font-size: 15px;
            line-height: 1.4;
        }//.master-picker__title_top

        &_bottom {
            font-family: $f-i;
            font-size: 14px;
            color: $color-text--light;
        }//.master-picker__title_bottom
    }//.master-picker__title

    .user {
        line-height: 1;
        margin-right: 20px;

        &__avatar {
            margin-bottom: 0;
        } //.user__avatar

        &__img {
            width: 35px;
            height: 35px;
        } //.user__img
    } //.user

    &__btn {
        margin-left: auto;
        padding: 10px 0 10px 10px;
        font-size: 17px;
        z-index: 0;
    } //.master-picker__btn
} //.master-picker

.time-picker {

    &__wrap {
        width: 100%;

        @include respond-min(md) {
            max-height: 276px;
            overflow: auto;
            border: 1px solid $color-border--lightest;
        }
    } //.time-picker__wrap

    &__item {
        @include flex;
        width: 100%;
        padding: 15px 20px;

        &:hover {
            box-shadow: inset 0 0 0 2px $color-main;
            border: none;
            cursor: pointer;
        }

        & + .time-picker__item {
            border-top: 1px solid $color-border--lightest;
        }

        i {
            font-size: 20px;

            @include respond-min(md) {
                display: none;
            }
        }

        .button-link {
            line-height: 1;
        }

        &.is-disabled {

            &:hover {
                box-shadow: none;
            }

            .time-picker__title {
                padding-right: 6px;
                color: $color-text--lighten;
            }

            i {
                display: none;
            }

            .button-link {
                color: $color-border--light;
            }
        }

        &.is-active {
            box-shadow: inset 0 0 0 2px $color-main;
        }
    } //.time-picker__item

    &__title {
        flex: 1;
        text-align: center;
        margin: auto;
        padding-left: 6px;
        color: $color-text--darkest;

        @include respond-min(md) {
            padding-left: 0;
        }
    } //.time-picker__title

    a {
        transition: none;
    }
} //.time-picker

.confirm-msg {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 15px;
    font-size: 15px;
    background-color: #f4f9f2;
    border: 1px solid #d7eacf;

    @include respond-min(md) {
        margin: 0;
        width: 100%;
    }

    &:hover {
        cursor: default;
    }

    &__icon {
        margin: 0 20px 0 10px;

        i {
            font-size: 40px;
            color: #6FB557;
        }
    } //.confirm-msg__icon

    &__title {
        font-family: $f-medium;

        @include respond-min(md) {
            margin-bottom: 5px;
        }
    } //.confirm-msg__title
} //.confirm-msg

.user-cart {
    width: 100%;

    @include respond-min(md) {
        box-shadow: $bxs;
        border-radius: $border-radius;
    }

    &__inner {
        width: 100%;
        background-color: #fff;
        transition: margin-left 0.5s;
        z-index: 10;
    } //.user-cart__inner

    &__top {
        position: relative;
        display: flex;
        flex-direction: column;
        padding: 20px;
        border-bottom: 1px solid $color-border--lightest;
        overflow: hidden;

        &.is-active {
            height: 86px;

            &:hover {
                cursor: default;
            }

            .user-cart {

                &__date-time {
                    top: 20px;
                    @include tr;
                } //.user-cart__date-time

                &__title {
                    font-family: $f-i;
                    color: $color-text--light;
                    font-size: 12px;
                    @include tr;
                } //.user-cart__title

                &__user {
                    margin-top: 27px;


                    .user__avatar {
                        margin-right: 0;
                    }

                    .user__img {
                       width: 0;
                       height: 0;
                       @include tr;
                    }
                } //.user-cart__user
            } //.user-cart
        }
    } //.user-cart__top

    &__bottom {
        display: flex;
        justify-content: space-between;
        position: absolute;
        bottom: 0;
        right: -100%;
        width: 100%;
        @include tr;
        background-color: $color-bg--light;

        @include respond-min(md) {
            position: relative;
            right: 0;
            padding: 10px;
            background-color: transparent;
            border-top: 1px solid $color-border--lightest;
        }

        .user-cart__title {
            font-family: $f-medium;
            text-transform: uppercase;
        } //.user-cart__title

        &.is-active {
            right: 0;
            @include tr;

            @include respond-min(md) {
                padding: 10px;
            }
        }
    } //.user-cart__bottom

    &__item {
        position: relative;
        display: flex;
        align-items: center;
        width: 100%;
        padding: 12.5px 35px;

        @include respond-min(md) {
            padding: 20px;
        }

        &:hover {
            cursor: default;
        }

        &.is-hover {

            &:hover {
                background-color: darken(#fff, 3%);
                cursor: default;

                .user-cart__inner {
                    margin-left: 32px;
                    transition: margin-left .5s;
                    background-color: $color-bg--lightest;
                }

                .icon {
                    left: 20px;
                }
            }
        } //.service-list__item.is-hover

        & + .user-cart__item {
            border-top: 1px solid $color-border--lightest;
        }

        .user-cart__title {
            font-family: $f-light;
            font-size: 15px;
            color: $color-main;
        } //.user-cart__title
    } //.user-cart__item

    &__details {
        display: flex;
        align-items: center;
        font-size: 14px;
    } //.user-cart__details

    &__left {
        width: 70%;
        font-family: $f-i;
        color: $color-text--light;
        font-size: 14px;
    } //.user-cart__left

    &__right {
        font-family: $f-medium;
        margin-left: auto;

        &--total {
            margin-left: auto;
            font-family: $f-medium;
            font-size: 18px;
        } //.user-cart__right--price
    } //.user-cart__right

    &__user {
        display: flex;
        align-items: center;

        .user {
            &__avatar {
                margin-bottom: 0;
                margin-right: 20px;
            } //.user__avatar

            &__img {
                width: 45px;
                height: 45px;
            } //.user__img
        }
    } //.user

    &__title {
        font-family: $f-medium;
        color: $color-text;
    } //.user-cart__title

    &__date-time {
        position: absolute;
        top: -100%;
    } //.user-cart__date-time

    &__note {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 11.5px 20px;

        @include respond-min(md) {
            padding: 10px;
        }
    } //.user-cart__desc

    &__btn {
        display: none;
        justify-content: space-between;
        position: relative;
        width: 100%;
        border-radius: 0;
        text-transform: uppercase;

        @include respond-min(md) {
            top: -100px;
            width: 100%;
            border-radius: $border-radius;
            z-index: -1;
            overflow: hidden;
        }

        &.is-active {
            display: flex;
            z-index: 1;
            top: 0;
            @include tr;
        }

        &.btn {
            padding: 12.5px 20px;

            @include respond-min(md) {
                padding: 10px 35px;
            }
        }
    } //.user-cart__btn

    &--confirmed {

        .user-cart__title {
            color: $color-text;
        } //.user-cart__title
    } //.user-cart--confirmed
} //.user-cart

.fa-ruble-sign {
    font-size: 0.85em;
    font-weight: 400;
} //.fa-ruble-sign

.shadow-box {
    width: 100%;
    padding: 20px;
    margin-bottom: 30px;
    box-shadow: $bxs;

    &:hover {
        cursor: default;
    }

    &__title {
        margin-bottom: 8px;
        font-family: $f;
        font-size: 18px;
        color: $gray--darkest;
    } //.service-box__title

    &__desc {
        font-family: $f-light;
        font-size: 15px;
    } //.shadow-box__desc
} //.shadow-box

.is-fixed {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;

    @include respond-min(md) {
        position: relative;
    }
} //.mobile-fixed

.service-list {
    width: 100%;
    padding-bottom: 50px;
    @include tr;
    @include flexColumn;

    &:hover {
        cursor: default;
    }

    &__title {
        margin-bottom: 20px;
        font-size: 18px;
    } //.service-list__title

    &__item {
        position: relative;
        width: 100%;
        padding: 15px 50px 15px 20px;
        border-bottom: 1px solid $color-border--lightest;

        @include respond-min(md) {
            box-shadow: $bxs;
            border-radius: $border-radius;
        }

        & + & {

            @include respond-min(md) {
                margin-top: 20px;
            }
        }
    } //.service-list__item

    &__icon {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);

        &i {

            @include respond-min(md) {
                font-size: 15px;
            }
        }

        &.fa-info-circle {
            left: -0.5px;
        }

        &.fa-map-marker-alt {
            left: 1px;
        }

        &.fa-clock {
            left: -0.5px;
        }
    } //.service-list__icon

    &__desc {
        margin-left: 30px;

        @include respond-min(md) {
            font-size: 14px;
        }
    } //.service-list__desc

    &__edit {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        right: 0;
        color: $color-main;
        width: 50px;
        height: 50px;

        @include respond-min(md) {
            display: none;
        }
    } //.service-list__edit

    &__row {
        position: relative;
        display: flex;
        align-items: center;

        & + & {
            margin-top: 10px;
        }
    } //.service-list__row

    &__btn {
        position: fixed;
        bottom: 60px;
        width: 100%;
        border-radius: 0;
        box-shadow: none;

        @include respond-min(md) {
            position: relative;
            border-radius: 3px;
            box-shadow: 0 2px 5px 0 #b7b7b7;
            bottom: 0;
            margin-top: 30px;
            width: auto;
            align-self: flex-end;
        }

        &:hover {
            transform: none;
            box-shadow: none;

            @include respond-min(md) {
                @include trfY(-1px);
                box-shadow: 0 3px 5px 0 #b7b7b7;
            }
        }
    } //.service-list__btn

    &--mobile {
        position: fixed;
        top: 50px;
        right: -100%;
        width: 100%;
        bottom: 0;
        background-color: #fff;
        overflow-y: auto;
    } //.service-list--mobile

    &.is-active {
        right: 0;
    }
} //.service-list

